/* Transcript Viewer Specific Styles */
.viewer-container {
  padding: 20px;
  background: var(--bg-main);
  position: relative;
}

/* Viewer Header */
.viewer-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  padding: 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.dark-mode .viewer-header {
  background: var(--brand-tertiary);
}

.back-btn {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 20px;
  background: linear-gradient(135deg, #9d8adb 0%, #4c4172 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(157, 138, 219, 0.4);
}

.viewer-title-section {
  flex: 1;
}

.viewer-title {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 8px;
}

.viewer-meta {
  display: flex;
  align-items: center;
  gap: 12px;
  flex-wrap: wrap;
}

.meta-badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 12px;
  background: linear-gradient(135deg, #9d8adb 0%, #4c4172 100%);
  color: white;
  border-radius: 12px;
  font-size: 12px;
  font-weight: 600;
}

.meta-info {
  color: var(--text-secondary);
  font-size: 14px;
}

.viewer-actions {
  display: flex;
  gap: 12px;
}

.action-btn-toolbar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  background: white;
  border: 2px solid rgba(197, 166, 249, 0.3);
  border-radius: 8px;
  color: var(--text-primary);
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dark-mode .action-btn-toolbar {
  background: var(--bg-main);
  border-color: rgba(197, 166, 249, 0.2);
}

.action-btn-toolbar:hover {
  background: linear-gradient(135deg, #9d8adb 0%, #4c4172 100%);
  color: white;
  border-color: transparent;
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(157, 138, 219, 0.4);
}

/* Annotation Toolbar */
.annotation-toolbar {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

.dark-mode .annotation-toolbar {
  background: var(--brand-tertiary);
}

.annotation-toolbar h3 {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.annotation-buttons {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.annotation-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 16px;
  background: rgba(197, 166, 249, 0.1);
  border: 2px solid rgba(197, 166, 249, 0.3);
  border-radius: 8px;
  color: var(--text-primary);
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.annotation-btn:hover {
  background: rgba(197, 166, 249, 0.2);
  border-color: rgba(197, 166, 249, 0.5);
  transform: translateY(-2px);
}

.annotation-btn.active {
  background: linear-gradient(135deg, #9d8adb 0%, #4c4172 100%);
  color: white;
  border-color: transparent;
  box-shadow: 0 4px 12px rgba(157, 138, 219, 0.4);
}

/* Zoom Controls */
.zoom-controls {
  position: fixed;
  top: 140px;
  right: 30px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: white;
  padding: 12px;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
  z-index: 50;
}

.dark-mode .zoom-controls {
  background: var(--brand-tertiary);
}

.zoom-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: linear-gradient(135deg, #9d8adb 0%, #4c4172 100%);
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.zoom-btn:hover:not(:disabled) {
  transform: scale(1.1);
  box-shadow: 0 4px 12px rgba(157, 138, 219, 0.4);
}

.zoom-btn:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

.zoom-level {
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  color: var(--text-primary);
  padding: 8px 0;
}

/* Document Wrapper */
.docx-document-wrapper {
  position: relative;
  background: white;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  padding: 40px;
  min-height: 600px;
}

.dark-mode .docx-document-wrapper {
  background: var(--brand-tertiary);
}

/* Document Content */
.docx-content {
  max-width: 850px;
  margin: 0 auto;
  background: white;
  padding: 60px 80px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  transition: transform 0.3s ease;
}

.dark-mode .docx-content {
  background: #1a1a1a;
  color: #e0e0e0;
}

/* Style the converted HTML content */
.docx-content h1 {
  font-size: 32px;
  font-weight: 700;
  margin: 24px 0 16px;
  color: var(--text-primary);
  line-height: 1.3;
}

.docx-content h2 {
  font-size: 26px;
  font-weight: 700;
  margin: 20px 0 12px;
  color: var(--text-primary);
  line-height: 1.3;
}

.docx-content h3 {
  font-size: 22px;
  font-weight: 600;
  margin: 18px 0 10px;
  color: var(--text-primary);
  line-height: 1.3;
}

.docx-content p {
  font-size: 16px;
  line-height: 1.8;
  margin: 12px 0;
  color: var(--text-primary);
  text-align: justify;
}

.docx-content ul,
.docx-content ol {
  margin: 12px 0;
  padding-left: 30px;
}

.docx-content li {
  font-size: 16px;
  line-height: 1.8;
  margin: 8px 0;
  color: var(--text-primary);
}

.docx-content strong,
.docx-content b {
  font-weight: 700;
}

.docx-content em,
.docx-content i {
  font-style: italic;
}

.docx-content a {
  color: var(--brand-secondary);
  text-decoration: underline;
}

.docx-content a:hover {
  color: var(--accent-primary);
}

.docx-content table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

.docx-content table th,
.docx-content table td {
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 10px 15px;
  text-align: left;
}

.dark-mode .docx-content table th,
.dark-mode .docx-content table td {
  border-color: rgba(255, 255, 255, 0.2);
}

.docx-content table th {
  background: rgba(157, 138, 219, 0.1);
  font-weight: 700;
}

/* Loading Spinner */
.loading-spinner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px;
  color: var(--text-primary);
}

.spinner {
  width: 60px;
  height: 60px;
  border: 5px solid rgba(157, 138, 219, 0.2);
  border-top-color: var(--brand-secondary);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.loading-spinner p {
  margin-top: 24px;
  font-size: 18px;
  font-weight: 600;
}

/* Error Message */
.error-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 80px;
  color: var(--text-primary);
  text-align: center;
}

.error-message p {
  margin: 12px 0;
  font-size: 16px;
}

.error-note {
  color: var(--text-secondary);
  font-size: 14px;
  font-style: italic;
  margin-top: 16px;
}

/* Annotations Panel */
.annotations-panel {
  margin-top: 40px;
  padding: 24px;
  background: rgba(157, 138, 219, 0.05);
  border-radius: 12px;
  border: 2px solid rgba(197, 166, 249, 0.3);
}

.annotations-panel h4 {
  font-size: 18px;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 16px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.annotation-item {
  padding: 16px;
  background: white;
  border-radius: 8px;
  border-left: 4px solid var(--brand-secondary);
  margin-bottom: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

.dark-mode .annotation-item {
  background: var(--bg-main);
}

.annotation-item:last-child {
  margin-bottom: 0;
}

.annotation-item.annotation-note {
  border-left-color: #fbbf24;
}

.annotation-item.annotation-highlight {
  border-left-color: #34d399;
}

.annotation-item.annotation-text {
  border-left-color: #60a5fa;
}

.annotation-header {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
  color: var(--text-secondary);
  font-size: 12px;
}

.annotation-text {
  color: var(--text-primary);
  font-size: 14px;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .viewer-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .viewer-actions {
    width: 100%;
  }

  .zoom-controls {
    right: 20px;
    top: 120px;
  }

  .docx-content {
    padding: 40px 30px;
  }
}

@media (max-width: 768px) {
  .viewer-header {
    padding: 16px;
  }

  .viewer-title {
    font-size: 20px;
  }

  .annotation-toolbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 12px 16px;
  }

  .zoom-controls {
    right: 10px;
    top: 100px;
  }

  .docx-document-wrapper {
    padding: 20px;
  }

  .docx-content {
    padding: 30px 20px;
  }

  .docx-content h1 {
    font-size: 26px;
  }

  .docx-content h2 {
    font-size: 22px;
  }

  .docx-content h3 {
    font-size: 18px;
  }

  .docx-content p,
  .docx-content li {
    font-size: 14px;
  }
}
